const _STATE = null;
const reducerBase = "content";
export const ContentReducer = (state = _STATE, action) => {
  switch (action.type) {
    case `${reducerBase}/set`:
      return {
        AboutUs: action.payload.AboutUs || state.AboutUs,
        ContactUs: action.payload.ContactUs || state.ContactUs,
        Footer: {
          Right: action.payload.Footer.Right || state.Footer.Right,
          Center: action.payload.Footer.Center || state.Footer.Center,
          Left: action.payload.Footer.Left || state.Footer.Left,
        },
      };
    case `${reducerBase}/reset`:
      return _STATE;
    default:
      return state;
  }
};

export default ContentReducer;
