import { Route } from "react-router-dom";
import HomePage from "./HomePage";
import { BrowserRouter, useMatch, useNavigate, Routes } from "react-router-dom";

function RouteWrapper({ Component }) {
  const match = useMatch(window.location.pathname);
  const navigate = useNavigate();
  return <Component match={match} redirect={navigate} />;
}

export function ApplicationRouter() {
  return (
    <>
      <div className="container-fluid ">
        <BrowserRouter>
          <Routes>
            <Route
              path="/"
              exact
              element={<RouteWrapper Component={HomePage} />}
            />
          </Routes>
        </BrowserRouter>
      </div>
    </>
  );
}
